import axios from 'axios';
import { make_subscription_url, make_unsubscription_url } from './constant';
import auth from '../auth';

const getConfig = () => {
  var config = {
    headers: { 'x-access-token': auth.getToken() },
  };
  return config;
};

export const submitAppointmentFunc = (id, email, paymentMethodId) => {
  const config = getConfig();

  const sendInfos = {
    email: email,
    payment_method: paymentMethodId,
  };
  return axios
    .post(`${make_subscription_url}/${id}`, sendInfos, config)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};

export const unsubscribeFunc = (id) => {
  const config = getConfig();

  return axios
    .delete(`${make_unsubscription_url}/${id}`, config)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};
