import axios from 'axios';
import { signup_url, signin_url, unique_url, profile_update, getuser_url, make_another_schedular } from './constant';

export const userLoginFunc = (userInfos) => {
  return axios
    .post(signin_url, userInfos)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};

export const userRegFunc = (userInfos) => {
  return axios
    .post(signup_url, userInfos)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};

export const uniqueUrlFunc = (url) => {
  return axios
    .get(`${unique_url}/?url=${url}`)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};

export const updateMeetingProfileFunc = (id, profileInfos) => {
  return axios
    .put(`${profile_update}/${id}`, profileInfos)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};

export const getUserByIdFunc = (id) => {
  return axios
    .get(`${getuser_url}/${id}`)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};

export const getUserByUrlFunc = (url, eventDuration) => {
  return axios
    .get(`${getuser_url}/${url}/${eventDuration}`)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};

export const makeSchedularEvent = (id, sendInfos) => {
  return axios
    .post(`${make_another_schedular}/${id}`, sendInfos)
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};
