import React from 'react';
import { Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions, Button } from '@material-ui/core';
import { deleteAppointmentFunc } from '../../actions/appointment';

const NewEventDialog = ({ open, handleClose, appointment, renderNewMeeting, setUpdate }) => {
  const handleSubmit = () => {
    deleteAppointmentFunc(appointment._id)
      .then((res) => {
        if (res.status === 200) {
          setUpdate(true);
          handleClose();
        }
      })
      .catch((e) => console.log(e));
  };

  return (
    <Dialog fullWidth={true} maxWidth="sm" open={open} onClose={handleClose}>
      <DialogTitle>Are you sure?</DialogTitle>
      <DialogContent>
        <DialogContentText>Confirm to cancel event.</DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose}>Exit</Button>
        <Button onClick={handleSubmit}>Confirm Cancel</Button>
      </DialogActions>
    </Dialog>
  );
};

export default NewEventDialog;
