import axios from 'axios';
import { get_days_available_url, get_timesolt_url } from './constant';
import auth from '../auth';

const getConfig = () => {
  var config = {
    headers: { 'x-access-token': auth.getToken() },
  };
  return config;
};

export const getDaysAvailableFunc = (currMonth, eventDuration, clientTz, url) => {
  const config = getConfig();

  return axios
    .get(
      `${get_days_available_url}?month=${currMonth}&meetTime=${eventDuration}&clientTz=${clientTz}&uniqueurl=${url}`,
      config,
    )
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};

export const getTimeSlotFunc = (month, date, eventDuration, clientTz, url) => {
  const config = getConfig();

  return axios
    .get(
      `${get_timesolt_url}?month=${month}&day=${date}&meetTime=${eventDuration}&clientTz=${clientTz}&uniqueurl=${url}`,
      config,
    )
    .then((response) => {
      return response;
    })
    .catch((err) => {
      return { error: err.response.data };
    });
};
